.navbar-header {
  .site-logo {
    margin-top: 11px;
  }
}

/*Custom Styling of the image uploader*/
#drop-area-div {
  width: 400px;
  height: 180px;
  border: 1px solid;
  text-align: center;
  padding-top: 120px;
  background-image: url("../../images/drag_and_drop.png");
  background-repeat: no-repeat;
  background-position: -4px -49px;
  background-size: 407px 253px;
  input {
    left: 119px;
    position: relative;
    top: 27px;
  }
}

#demo-files > div {
  margin-bottom: 10px;
}

img.demo-image-preview {
  float: left;
  margin-right: 10px;
  margin-top: 4px;
  height: 56px;
  width: 56px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
}

.demo-file-id {
  font-weight: bold;
}

.demo-file-size {
  font-style: italic;
  color: gray;
  font-size: 90%;
}

.demo-file-status-success {
  color: #009900;
}